// ----------------------------------------------------

:root {
  --text: #42505c;
  --background: #f5f7fa;
  --lineGrey: #dcdcdc;
  --borderLineGrey: #e9e9e9;
  --borderHeader: #e4e4e4;
  --emptyTextField: #a8b1be;
  --primaryGrey: #303d4f;
  --softGrey: #3c4b64;
  --hoverDarkGrey: #3c4c63;
  --green: #4abe84;
  --softGreen: rgb(242, 250, 246) !important;
  --red: #eb5c5c;
  --nextPagination: #969ea1;

  --fontSizeTitle: 18px;
  --fontSizeButton: 16px;
  --fontSizeWeightNumber: 20px;
  --fontSizeSmallTitle: 10px;
  --fsUserIconHeader: 35px;
}

// Color
.border-green {
  border: 1px solid !important;
  border-color: var(--green) !important;
}

.border-red {
  border: 1px solid !important;
  border-color: var(--red) !important;
}

.text {
  color: var(--text) !important;
}
.c-text {
  font-size: 1rem;
}

.borderHeader {
