// scroll-bar

::-webkit-scrollbar {
  width: 13px;
  height: 13px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--lineGrey);
  border: 3px solid transparent;
  border-radius: 10px;
  background-clip: content-box;
}
