}

.h-screen {
  height: 90vh;
}

.h-screen-title {
  height: 6vh;
  overflow: hidden;
}

.h-screen-content {
  height: 85vh;
  max-height: 85vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

