/* Tooltip container */
.tooltip-custom {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip-custom .tooltiptext {
  visibility: hidden;
  min-width: 150px;
  background-color: black;
  color: #fff;

  padding: 5px;
  border-radius: 6px;
  margin-left: 3.5rem;
  margin-top: -5px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip-custom:hover .tooltiptext {
  visibility: visible;
}

.tooltip-custom .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}

.hiddenTooltip.tooltip-custom:hover .tooltiptext {
  visibility: hidden;
}

