/* Tooltip container */
.tooltips {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltips .tooltiptext {
  visibility: hidden;
  min-width: 150px;
  background-color: black;
  color: #fff;

  padding: 5px;
  border-radius: 6px;
  margin-left: -10.5rem;
  margin-top: -5px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltips:hover .tooltiptext {
  visibility: visible;
}

.tooltips .tooltiptext::after {
  content: " ";
  position: absolute;
  transform: rotate(180deg);
  top: 50%;
  right: -6.5%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}

.hiddenTooltip.tooltips:hover .tooltiptext {
  visibility: hidden;
}

